  loadBitstamp(){
    (async () => {
      let exchange = new ccxt.bitstamp ({ proxy: 'https://cors.io/?' })
      let markets = await exchange.load_markets (true)
      let OrderBook = await exchange.fetchOrderBook(this.state.pair)
      let bestPrice = await exchange.fetchTicker(this.state.pair)
      

      this.setState({ 
        BitstampBids: OrderBook['bids'].slice(1,SIZE),
        BitstampAsks: OrderBook['asks'].slice(1,SIZE),
        BitstampBestBid: bestPrice['bid'],
        BitstampBestAsk: bestPrice['ask']
      })
      
  })()
  }
  

